.home {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-color: rgb(255, 255, 255);
}

.home__header {
     display: flex;
     justify-content: space-between;
     padding: 20px 30px;
     align-items: center;
}

.home__header a {
    margin-right: 20px;
    text-decoration: inherit;
    columns: rgba(0,0,0,0.87);;
    font-size: 15px;
}

.home__header a:hover {
    text-decoration: underline;
}

.home__headerRight {
    display: flex;
    align-items: center;
    min-width: 13wv;
    justify-content: space-between;
}

.home__headerRight > .MuiSvgIcon-root {
    margin-right: 20px;
}

.home__body {
    flex: 1;
    display: flex;
    margin-top: 10%;
    flex-direction: column;
}

.home__body > img {
    /* margin: 0 0; */
    object-fit: scale-down;
    transform: scale(2.65);
    height: 105px;
    margin-bottom: 0;
    padding: 0px;
    /* width: 1500px; */
}

.credits {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: rgb(0, 132, 234);
    font-size: 10px;
    object-fit: contain;
    margin-top: 4px;
    margin-left: 140px;
}